<template>
  <div class="g-login-box">
    <van-nav-bar
      title=""
      left-arrow
      @click-left="toPageReturn"
    />
    <img class="u-bg" src="./../assets/logo.png">
    <van-form>
      <van-field
        v-model="state.user"
        name=""
        label=""
        placeholder=""
        :rules="[{ required: false, message: '' }]"
      />
      <van-field
        name=""
        label=""
        placeholder=""
        :rules="[{ required: false, message: '' }]"
      />
      <div style="margin: 16px;">
        <van-button round block type="primary" native-type="submit" @click="login">
          
        </van-button>
      </div>
    </van-form>
  </div>
</template>

<script lang="ts">
import { defineComponent, reactive } from 'vue'
import router from './../router'
import api from '../api'

export default defineComponent({
  setup () {
    const state = reactive({
      value: '',
      showPicker: false,
      user: ''
    })
    const toPage = () => {
      if (state.user.includes('2')) {
        router.push('/hello-world?type=2')
      } else {
        router.push('/hello-world?type=1')
      }
    }
    const login = () => {
      api.auth.postAuthLogin({}).then(res => {
        toPage()
      }).finally(() => {
        toPage()
      })
    }
    const toPageReturn = () => router.go(-1)
    return {
      state,
      toPage,
      toPageReturn,
      login
    }
  }
})
</script>

<style scoped>
.u-bg {
  width: 100%;
  height: 100px;
}
</style>
