
import { defineComponent, reactive } from 'vue'
import router from './../router'
import api from '../api'

export default defineComponent({
  setup () {
    const state = reactive({
      value: '',
      showPicker: false,
      user: ''
    })
    const toPage = () => {
      if (state.user.includes('2')) {
        router.push('/hello-world?type=2')
      } else {
        router.push('/hello-world?type=1')
      }
    }
    const login = () => {
      api.auth.postAuthLogin({}).then(res => {
        toPage()
      }).finally(() => {
        toPage()
      })
    }
    const toPageReturn = () => router.go(-1)
    return {
      state,
      toPage,
      toPageReturn,
      login
    }
  }
})
