import Axios from 'axios'

const ajax = (data: any = {}, { url, method }:any, showErrMsg = true) => {
  return new Promise((resolve, reject) => {
    Axios({
      url,
      method,
      params: data.query || (method === 'get' ? data : {}),
      data: data.body || (method === 'post' ? data : {}),
      timeout: 1000 * 60,
      headers: {
        ...data.headers
      }
    }).then(res => {
      if (Number(res.data.code) !== 200) {
        // 
        // 
        if (showErrMsg) console.log(res.data.message)
        reject(res.data)
        return false
      }
      resolve(res.data)
    }).catch(err => {
      // http
      // http
      reject(err.data)
      if (err.message === undefined) return false
      if (err.toString().indexOf('401') !== -1) {
        console.log('') // 
        return false
      }
      if (showErrMsg) console.log('!') // !
    })
  })
}

export default ajax
